<template>
  <div class="flex h-full min-h-[500px] w-full flex-col">
    <GeneralForm
      :heading="'Quotation Upload'"
      :sub-heading="'Select the products for which you want to upload quotation'"
      :show-close-button="true"
      :submit-button-label="'Confirm'"
      @close-pressed="handleClose"
      @form-submitted="handleSubmit"
      class="flex-1"
    >
      <template #form-body>
        <!-- Products Selection -->
        <div class="flex flex-wrap gap-spacing-300">
          <div
            v-for="prod in getNotSelectedProducts"
            :key="prod?._id"
            class="flex cursor-pointer items-center gap-spacing-100 text-body/m/relaxed/400 text-content-on-neutral-primary"
            @click="handleSelect(prod)"
          >
            <GeneralCheckbox :selected="selectedProducts?.includes(prod._id)" />
            <p>
              <span>{{ prod?.title }}</span>
              <span v-if="prod?.variableName"> - {{ prod?.variableName }}</span>
            </p>
          </div>
        </div>

        <GeneralFileUpload
          v-if="getNotSelectedProducts?.length > 0"
          :accepted-file-types="acceptedFileTypes"
          :allow-multiple="false"
          aria-label="Upload Quotation"
          @files-uploaded="processFiles"
          class="mt-spacing-300"
        />

        <!-- Uploaded File Details -->
        <ClassicTable
          :headers="quotationHeaders"
          v-if="quotationFileData?.length > 0"
          class="mt-spacing-200"
          :horizontal-padding="'px-spacing-0'"
          :bgClass="'bg-transparent'"
        >
          <template #table-body>
            <tr
              v-for="(quotation, index) in quotationFileData"
              :key="index"
              class="border-b border-stroke-medium text-left text-body/m/relaxed/400 text-content-on-neutral-primary"
            >
              <td
                class="max-w-[200px] py-spacing-100 pr-spacing-100"
                :class="
                  quotation?.file?.isLink
                    ? 'text-content-on-neutral-tertiary'
                    : 'text-content-on-neutral-primary'
                "
              >
                <p>
                  {{ quotation?.file?.name }}
                  <span
                    v-if="!quotation?.file?.isLink"
                    class="ml-spacing-50 rounded-04 bg-bg-status-success px-spacing-50 py-spacing-0 text-overline/s/relaxed/500 text-content-on-status-on-success"
                  >
                    NEW
                  </span>
                </p>
              </td>

              <!-- Associated Products -->
              <td class="py-spacing-100 pr-spacing-100">
                <div class="flex flex-wrap items-center gap-spacing-50">
                  <div
                    v-for="prod in quotation?.products"
                    :key="prod?._id"
                    class="text-content-on-neutral-primary"
                  >
                    <GeneralRemovableItem
                      :item-name="`${prod?.title}${
                        prod?.variableName ? ' - ' : ' - '
                      }${prod?.variableName || 'default[1]'}`"
                      @on-item-removed="removeProductQuotation(prod)"
                      :is-disabled="quotation?.file?.isLink"
                    />
                  </div>
                </div>
              </td>

              <!-- Remove Button -->
              <td class="py-spacing-100 pr-spacing-100">
                <FileActions
                  :file="quotation?.file"
                  @remove="removeQuotation(index)"
                />
              </td>
            </tr>
          </template>
        </ClassicTable>
      </template>
    </GeneralForm>
  </div>
</template>

<script>
import { FILE_VALIDATION_OBJECT } from "@/utilities/constants/constants";
import { mapActions } from "vuex";

export default {
  props: {
    products: {
      type: Array,
      required: true,
    },
    quotationFiles: {
      type: Array,
      required: true,
    },
  },
  components: {
    GeneralForm: () => import("@/components/General/Form.vue"),
    GeneralCheckbox: () => import("@/components/General/Checkbox.vue"),
    GeneralFileUpload: () => import("@/components/General/FileUpload.vue"),
    ClassicTable: () => import("@/components/General/Table/ClassicTable.vue"),

    GeneralRemovableItem: () =>
      import("@/components/General/RemovableItem.vue"),
    FileActions: () =>
      import("@/components/Utility/FileViewer/FileActions.vue"),
  },
  data() {
    return {
      selectedProducts: [],
      quotationFileData: this.quotationFiles,
      quotationHeaders: [
        { key: "file", value: "File", sortable: false },
        { key: "products", value: "Products", sortable: false },
        { key: "action", value: "", sortable: false },
      ],
    };
  },
  watch: {
    quotationFiles() {
      if (this.quotationFiles) {
        this.quotationFileData = this.quotationFiles;
      }
    },
  },
  computed: {
    getNotSelectedProducts() {
      // Filter out products already included in uploaded files
      const uploadedProductIds = new Set(
        this.quotationFileData.flatMap((q) => q.products.map((p) => p._id)),
      );
      return this.products.filter((prod) => !uploadedProductIds.has(prod?._id));
    },
    acceptedFileTypes() {
      return FILE_VALIDATION_OBJECT?.QUOTATION?.ALLOWED_FILE_EXTENSIONS;
    },
  },
  methods: {
    ...mapActions("utils", ["getFileBlob"]),
    handleSelect(prod) {
      const isSelected = this.selectedProducts.includes(prod._id);
      if (isSelected) {
        this.selectedProducts = this.selectedProducts.filter(
          (id) => id !== prod._id,
        );
      } else {
        this.selectedProducts.push(prod._id);
      }
    },
    processFiles(files) {
      if (this.selectedProducts?.length === 0) {
        return this.$toast.error(
          "Please select at least one product for uploading quotation",
        );
      }
      files.forEach((file) => {
        this.quotationFileData.unshift({
          file,
          products: this.products.filter((prod) =>
            this.selectedProducts.includes(prod?._id),
          ),
        });
      });

      this.selectedProducts = [];
    },
    removeQuotation(index) {
      this.quotationFileData.splice(index, 1);
    },
    removeProductQuotation(product) {
      this.quotationFileData = this.quotationFileData
        .map((item) => ({
          ...item,
          products: item.products?.filter((prod) => prod?._id !== product?._id),
        }))
        ?.filter((item) => item?.products?.length > 0);
    },
    handleClose() {
      this.$bus.$emit("product-quotation-select-modal", false);
    },
    handleSubmit() {
      this.$bus.$emit(
        "attach-quotations-to-status-form",
        this.quotationFileData,
      );
      this.handleClose();
    },
  },
};
</script>
